$primary-color: #004838;
$secondary-color: #E2FB6C;
$button-hover-color: darken($primary-color, 10%);

@mixin flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar {
    max-height: 30px;
    position: sticky;
    top: 0;
    z-index: 9999;
    @include flex-center;
    justify-content: space-between;
    padding: 20px 40px;
    background-color: #fff;

    &.sticky {
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    }
}

.logo {
    margin-left: 5%;
    @include flex-center;

    .logoImage {
        margin-right: 10px;
    }

    .logo-text {
        font-size: 20px;
        font-weight: bold;
        color: #030e09;
    }
}

.nav-actions {
    @include flex-center;
    margin-right: 20px;
}

.nav-links {
    list-style: none;
    @include flex-center;
    margin-right: 20px;

    li {
        margin-right: 20px;

        a {
            text-decoration: none;
            font-size: 16px;
            color: $primary-color;
        }
    }
}

.contact-link {
    text-decoration: none;
    font-size: 16px;
    color: $primary-color;
    margin-right: 20px;
}

@media (max-width: 768px) {
    .logo {
        margin-left: 0%;
    }

    .nav-actions {
        margin-right: 20px;
    }

    .nav-links {
        margin-right: 10px;

        li {
            margin-right: 10px;
        }
    }
}