.contentContainer {
    width: 41%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 3%;
}

.subtitleContainer {
    display: flex;
    align-items: center;
}

.subtitle {
    font-size: 16px;
    font-weight: 400;
}

.title {
    margin-bottom: 1%;
    font-weight: 500;
    font-size: 44.1px;
    margin-top: 1%;
}

.hirelystSpan {
    font-family: Libre Caslon Text;
    font-size: 44.1px;
    font-style: italic;
    font-weight: 400;
    letter-spacing: 0.5px;
    text-align: left;
}

.description {
    margin-bottom: 4%;
    font-size: 16px;
    font-weight: 400;
    text-align: left;
}

.arrow {
    margin-left: 5px;
}

.graphImage {
    width: 30%;
    border-radius: 15px;
    margin-right: 4%;
    margin-top: 3%;
    margin-bottom: 3%;
}

.logoImage {
    width: 4%;
    margin-right: 10px;
}

@media (max-width: 768px) {
    .outerCard {
        height: auto;
        margin-left: 5%;
        margin-right: 5%;
    }

    .innerCard {
        flex-direction: column;
        height: auto;
        width: 100%;
        padding: 4%;
    }

    .contentContainer {
        width: 100%;
        margin-left: 0;
        margin-bottom: 5%;
    }

    .title,
    .hirelystSpan {
        font-size: 32px;
    }

    .description {
        margin-bottom: 5%;
    }

    .graphImage {
        display: none;
    }

    .logoImage {
        width: 6%;
    }

    .arrow {
        display: none;
    }
}