.footerContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    grid-template-columns: 1fr 3fr;
    align-items: start;
    width: 100%;
    margin-top: 15%;
    color: #004838;
}

.logoContainer {
    display: flex;
    align-items: center;
}

.description {
    margin-top: 0%;
    margin-bottom: 20%;
}

.logoImage {
    width: 15%;
    margin-right: 10px;
    color: #004838;
}

.brandSection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    width: 15%;
    margin-left: 2%;
}

.icons {
    display: flex;
    gap: 10px;
}

.linksSection {
    display: flex;
    justify-content: space-between;
    width: 50%;

}

.linksBlock {
    display: flex;
    flex-direction: column;
    list-style: none;
    padding: 0;
    margin: 0;

    h3 {
        margin-bottom: 1rem;
    }

    ul {
        list-style: none;
        padding: 0;
        margin: 0;

        li {
            margin-bottom: 1rem;

            a {
                color: inherit;
                text-decoration: none;

                &:hover {
                    text-decoration: none;
                }
            }
        }
    }
}