.card {
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
    margin-bottom: 10px;

    .buffer {
        padding: 20px;
    }

    .card-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-weight: bold;
        color: #030E09;
        cursor: pointer;
        font-size: 20.2px;
        font-weight: 500;
        line-height: 34px;
    }

    .toggle-icon {
        color: #030E09;
        width: 24px;
        height: 24px;
        margin-right: 1%;
        transition: transform 0.3s ease-in-out;
    }

    .card-body {
        transition: max-height 0.3s ease-in-out, margin-top 0.3s ease-in-out;
        color: #030E09;
        max-height: 0;
        overflow: hidden;
        font-size: 16px;
        font-weight: 400;
        line-height: 27.2px;
    }

    &.open .card-body {
        margin-top: 3%;
    }

    &.open .toggle-icon {
        transform: rotate(180deg);
    }
}