.contentContainer {
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.title {
    margin-bottom: 1%;
    margin-top: 1%;
    font-size: 54.7px;
    font-weight: 500;
    line-height: 75px;
    letter-spacing: -2.5px;
    text-align: left;

}

.hirelystSpan {
    font-family: Libre Caslon Text;
    font-size: 54.7px;
    font-style: italic;
    font-weight: 400;
    line-height: 75px;
    letter-spacing: -2.5px;
    text-align: left;
}

.description {
    margin-bottom: 4%;
    font-size: 18px;
    font-weight: 400;
    text-align: left;
    line-height: 30.6px;
    width: 70%;
}

.arrow {
    margin-left: 5px;
}

.graphImage {
    width: 45%;
    border-radius: 15px 0 0 0;
    margin-right: -5.8%;
    margin-top: 3%;
    margin-bottom: 3%;
}

.ratingImage {
    width: 40%;
    border-radius: 15px;
    margin-right: -7%;
    margin-top: 5%;
}

.incomeImage {
    width: 15%;
    border-radius: 15px;
    margin-right: -57%;
    margin-top: -32%;
    z-index: 1;
}

.recordImage {
    width: 15%;
    border-radius: 10px;
    margin-right: -18.5%;
    margin-top: 30%;
    z-index: 1;
    opacity: 0.8;
}

@media (max-width: 768px) {
    .outerCard {
        height: auto;
        margin-left: 5%;
        margin-right: 5%;
    }

    .innerCard {
        flex-direction: column;
        height: auto;
        width: 100%;
        padding: 4%;
    }

    .contentContainer {
        width: 100%;
        margin-left: 0;
        margin-bottom: 5%;
    }

    .title,
    .hirelystSpan {
        font-size: 32px;
    }

    .description {
        margin-bottom: 5%;
    }

    .graphImage,
    .ratingImage,
    .incomeImage,
    .recordImage {
        display: none;
    }

    .arrow {
        display: none;
    }
}