.outerCard {
    background-color: #fff;
    height: 80vh;
    border-radius: 20px;
    margin-left: 2%;
    margin-right: 2%;
    box-shadow: none;
}

.innerCard {
    background-color: #004838;
    width: 96%;
    height: 60vh;
    border-radius: 20px;
    margin-left: 2%;
    margin-right: 2%;
    color: white;
    padding: 2%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.parentContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: left;
    margin-left: 4%;
}

.benefitHeader {
    width: 40%;
}

.benefitsSpan {
    font-family: "Libre Caslon Text";
    font-size: 45.99px;
    font-style: italic;
    font-weight: 400;
    line-height: 65.07px;
    letter-spacing: -1.7727280855178833px;
    text-align: left;
}

.contentContainer {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 3%;
}

.title {
    margin-bottom: 1%;
    font-weight: 500;
    font-size: 44.1px;
    margin-top: 1%;
}

.hirelystSpan {
    font-family: "Libre Caslon Text";
    font-size: 44.1px;
    font-style: italic;
    font-weight: 400;
    letter-spacing: 0.5px;
    text-align: left;
}

.description {
    margin-bottom: 4%;
    font-size: 16px;
    font-weight: 400;
    text-align: left;
}

.checkmark {
    width: 25.03px;
    height: 25.03px;
    padding: 4.17px;
    gap: 0px;
    border-radius: 25.03px 0px 0px 0px;
    opacity: 0px;

}

.featureList {
    list-style: none;
    margin-left: -5%;
}

.featureItem {
    font-size: 16px;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.checkmark {
    margin-right: 10px;
    display: flex;
    align-items: center;
}


.graphImage {
    width: 30%;
    border-radius: 15px;
    margin-right: 3%;
    margin-top: 3%;
    margin-bottom: 3%;
}

.arrowContainer {
    display: flex;
    justify-content: right;
    align-items: center;
    gap: 20px;
    margin-right: 4%;
    margin-bottom: 2%;
}

.arrowButton {
    background-color: #004838;
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 0.3s;
}

.arrowIcon {
    color: white;
    font-size: 24px;
}


@media (max-width: 768px) {
    .outerCard {
        height: auto;
        margin-left: 5%;
        margin-right: 5%;
    }

    .innerCard {
        flex-direction: column;
        height: auto;
        width: 100%;
        padding: 4%;
    }

    .contentContainer {
        width: 100%;
        margin-left: 0;
        margin-bottom: 5%;
    }

    .title,
    .hirelystSpan {
        font-size: 32px;
    }

    .description {
        margin-bottom: 5%;
    }

    .graphImage {
        display: none;
    }

    .arrow {
        display: none;
    }

    .benefitHeader {
        width: 90%;
    }
}