.contentContainer {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 3%;
}

.buttonContainer {
    display: flex;
    align-items: center;
    margin-right: 5.5%;
}

.title {
    margin-bottom: 1%;
    margin-top: 1%;
    color: #BCEC88;
    font-size: 44.4px;
    font-weight: 500;
    line-height: 62.4px;
    letter-spacing: -1.7px;
    text-align: left;
}

.hirelystSpan {
    font-family: Libre Caslon Text;
    font-size: 44.1px;
    font-style: italic;
    font-weight: 400;
    letter-spacing: 0.5px;
    text-align: left;
}

.expandableDiv {
    width: 45%;
    margin-right: 5%;
}

.description {
    margin-bottom: 4%;
    font-size: 18px;
    font-weight: 400;
    text-align: left;
    color: white;
    width: 60%;
}

.arrow {
    margin-left: 5px;
}

.laptopImage {
    width: 48%;
    margin-bottom: -2%;

}

@media (max-width: 768px) {
    .contentContainer {
        width: 100%;
        margin-left: 0;
        margin-bottom: 5%;
    }

    .title,
    .hirelystSpan {
        font-size: 32px;
    }

    .description {
        margin-bottom: 5%;
        width: 90%;

    }

    .laptopImage {
        display: none;
    }

    .arrow {
        display: none;
    }

    .expandableDiv {
        width: 90%;
        margin-right: 5%;
        margin-left: 5%;
    }

    .contentContainer {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-left: 3%;
    }

}